<template>
  <div id="quesion" class="scrollTouch" :class="{hasHeader: $utils.showNavBar()}">
    <HeaderNav v-if="$utils.showNavBar()"></HeaderNav>
    <div class="topQuesion bgIcon">
      <!-- <div class="top_title">掘金宝</div>
      <div class="top_title1">教你快速累积百万财富</div>
      <div class="top_title2">QQ交流群：827745296</div> -->
    </div>
    <div class="quesionContent" v-for="(item, index) in questionArr" :key="index">
      <!-- 使用必看 -->
      <div class="questionBigTitle flex-row">{{item.title}}</div>
      <!-- 第一条 -->
      <div class="secItem" v-for="(question, index1) in item.secQues" :key="index1">
         <div class="questionTitle flex-row" @click="setShow(index, index1)">
            <div class="flex-row"><span>{{question.title}}</span><span class="tag" v-if="question.tag">{{question.tag}}</span></div>
            <div class="arrow" :class="{active: question.active === 1}"></div>
          </div>
          <div class="wrapper" :ref="`wrapper${index}${index1}`" :style="question.active === 1 ? `height:${question.height}`: 'height:0'">
            <div class="textWrapper">
              <div class="text" @click="jumpTo()" v-for="(text, index2) in question.content" :key="index2" v-html="text"></div>
            </div>
          </div>
      </div>
    </div>
  </div>
</template>
<script>
import HeaderNav from '@/components/task/header-nav'
export default {
  components: {
    HeaderNav
  },
  data () {
    return {
      questionArr: [{
        title: '使用必看',
        secQues: [
          {
            title: '异常行为处理',
            content: [
              `平台用先进的人工智能分析用户行为，如发现造假等违规操作，
              平台有权阻止你使用（填写邀请码、领取金币、提现、提成）以
              及取消你获得的奖励。具体违规行为包括但不限于下面列举：`,
              `使用具有作弊嫌疑的高危手机号；<br>使用同一个手机反复登录多个账号；<br>使用同一个手机填写过多次邀请码；<br>使用模拟器；<br>使用App分身软件；`
            ]
          }
        ]
      }, {
        title: '金币',
        secQues: [
          {
            title: '金币是什么? 掘金宝的价值？',
            content: [
              `金币是掘金宝app里面的货币单位。每天所赚取的金币会自动转换成掘金宝（原始股），并累计到“我的钱包”里。`,
              `掘金宝（原始股）将随着用户增多而不断升值，用户可在上市后交易变现。`,
              `上市前，平台奖励每两个月减半一次，越早参与，奖励越多。`
            ]
          },
          {
            title: '什么任务赚金币快？',
            content: [
              `【邀请好友】赚原始股最快！您可通过微信、QQ等方式邀请好友为你助力冲榜。`,
              `邀请无上限，邀请越多，奖励越多。`,
              `好友每日阅读，你还能得提成奖励，只要邀请足够多，躺着也能赚金币！`
            ]
          },
          {
            title: '金币如何兑换为掘金宝？汇率是多少？',
            content: [
              `当天获得的金币会在晚上自动换成掘金宝存入您的账户。`,
              `掘金宝收益=昨日金币/汇率，当前固定汇率为：10000金币=1个掘金宝（1股原始股）；`,
              `您也可以在【我的】页面，点击【我的钱包】查看昨天入账的掘金宝。`
            ]
          },
          {
            title: '如何获得金币/掘金宝？',
            content: [
              `可以进入【任务】页面，完成每日任务获得金币/掘金宝`
            ]
          },
          {
            title: '在哪查看收益？',
            content: [
              `可以在【我的】页面里，看目前累积的财富收入`
            ]
          },
          {
            title: '签到奖励？',
            content: [
              `每日签到是给新老用户的福利，七天为一个周期，金币递增，第七日最高，每日打开掘金宝app，自动签到。`
            ]
          }
        ]
      }, {
        title: '邀请好友',
        secQues: [
          {
            title: '邀请好友有什么好处？',
            content: [
              `邀请好友后，不仅每个好友奖励你高额原始股，好友阅读文章获得的金币会翻倍提成给你。`,
              `你邀请越多，每天收到的金币也越多。`
            ]
          },
          {
            title: '手把手教你邀请好友',
            tag: '热门',
            content: [
              `<a class='link' data-url='/invite_friend' @click='toPage'>邀请好友</a>`
            ]
          },
          {
            title: '邀请奖励如何获得？',
            content: [
              `<div class='method'><span>方法1：</span><span>分享平台任何页面到任意微信群、朋友圈，新用户点击登录，你即可获得奖励</span></div>`,
              `<div class='method'><span>方法2：</span><span>让好友打开应用市场，搜索掘金宝资讯，下载后登录填写邀请码。</span></div>`
              // `邀请奖励分3天发放（好友当天认真阅读3篇文章或视频发放1次，次日凌晨统一发放）`,
              // `邀请奖励30天有效，期间好友完成3天阅读，你和他还能再得金币奖励。`
            ]
          },
          {
            title: '为什么邀请了没有收到奖励？',
            content: [
              `以下可能的原因供参考：`,
              `原因①：邀请的好友必须是从未登录过的新用户（同一个微信号、手机号视为同一个用户）`,
              `原因②：好友进入掘金宝app后，必须填写你的邀请码才算邀请成功`,
              // `原因③：邀请奖励分3天发放，徒弟每天认真阅读3篇发放一次（认真阅读3篇）`,
              `原因③：如果奖励没有及时到账，别担心，可能会有点延迟`
            ]
          },
          {
            title: '什么是邀请码? 怎么用？',
            content: [
              `邀请码是掘金宝app为每一位用户设计的独立ID号。`,
              `邀请码只在邀请好友时使用。`,
              `使用邀请码的两种情况：`,
              `a.好友注册账号之后，直接复制您的邀请码或手工输入邀请码。b.通过微信、QQ等软件分享邀请码链接给朋友，不需要填写邀请码，更加方便。`
            ]
          },
          {
            title: '在哪获得邀请码？在哪输入邀请码？',
            content: [
              `获得邀请码：你可以向你的好友或者你的推荐人询问邀请码；`,
              `输入邀请码：获得他人的邀请码后，进入掘金宝app，在【任务】，找到“输入邀请码任务”，点击即可输入。`
            ]
          },
          {
            title: '在哪查看我的邀请码？',
            content: [
              `点击【我的】或【邀请好友】页面，查看你的邀请码，把邀请码给你的朋友，进入掘金宝资讯App填写。`,
              `好友进入【任务】，填写你的邀请码后，他每日阅读的金币就可以双倍提成给你`
            ]
          },
          {
            title: '为什么输入邀请码提示错误？',
            content: [
              `可能的原因：`,
              `① 需要确保邀请码是准确无误的`,
              `② 不能填写自己的邀请码`,
              `③ 新用户（新手机、新账号）注册掘金宝app后，30天有效，超过30天就不能填写了`
            ]
          },
          {
            title: '为什么不能输入邀请码？',
            content: [
              `邀请码只能绑定一个邀请人（即：输入谁的邀请码，谁就是你的推荐人），所以对于已经推荐人的用户不能再输入邀请码`
            ]
          }
        ]
      }, {
        title: '变现',
        secQues: [
          {
            title: '什么时候可变现？',
            content: [
              `上市后，就可以将你持有的掘金宝（原始股）交易变现了`,
              `上市前，每两个月掘金宝奖励减半，越早参与，奖励越丰厚。`,
              `你可在【赚车赚房】页面，查看掘金宝详细内容。`
            ]
          }
        ]
      }]
    }
  },
  methods: {
    back () {
      this.$router.go(-1)
    },
    // 设置激活与不激活
    setShow (index, index1) {
      if (this.questionArr[index].secQues[index1].active === 1) {
        this.$set(this.questionArr[index].secQues[index1], 'active', 0)
      } else {
        this.$set(this.questionArr[index].secQues[index1], 'active', 1)
      }
    },
    jumpTo (e) {
      e = e || window.event
      let target = e.target || e.srcElement
      if (target.getAttribute('data-url')) {
        this.$router.push(target.getAttribute('data-url'))
      }
    },
    // 计算每一个的高度
    countHeight () {
      for (let [i, v] of this.questionArr.entries()) {
        for (let j of v.secQues.keys()) {
          let temp = this.$refs[`wrapper${i}${j}`][0].getElementsByClassName('textWrapper')[0]
          this.$set(this.questionArr[i].secQues[j], 'height', temp.clientHeight + 'px')
        }
      }
    },
    // 设置高度
    setHeight () {
      this.$nextTick(() => {
        this.countHeight()
      })
    }
  },
  mounted () {
    this.setHeight()
  }
}
</script>
<style lang="scss" scoped>
#quesion{
  overflow: auto;
  height: 100%;
  box-sizing: border-box;
  .secItem:nth-child(2) > .questionTitle{
    box-sizing: border-box;
    padding-bottom: 10px;
    border-top: 0;
  }
  .topQuesion{
    height: 347px;
    background-image: url('../../../assets/img/task/quesion.png');
    color: #FFFFFF;
    text-align: center;
    border-top: 1px solid transparent;
    >div{
      margin: 40px 0;
    }
    .top_title{
      font-size: 72px;
    }
    .top_title1 {
      font-size: 58px;
    }
    .top_title2 {
      font-size: 30px;
    }
  }
  .quesionContent{
    font-size: 30px;
    .questionBigTitle {
      justify-content: flex-start;
      align-items: flex-end;
      padding: 0 30px 10px;
      box-sizing: border-box;
      color: #FF7F18;
      height: 80px;
      &::before{
        content: '';
        width: 4px;
        height: 30px;
        margin-right: 10px;
        display: inline-block;
        background: #FF7F18;
      }
    }
    .questionTitle{
      height: 90px;
      padding: 0 30px;
      border-top: 1px solid #ddd;
      border-bottom: 1px solid #ddd;
      justify-content: space-between;
      margin-top: -1px;
      .tag{
        background: rgba($color: #FF0000, $alpha: .8);
        border-radius: 8px;
        padding: 4px 10px;
        line-height: 32px;
        margin-left: 20px;
        font-size: 20px;
        color: #fff;
      }
      .arrow {
        width: 22px;
        height: 22px;
        background-image: url('../../../assets/img/task/arrow_bottom.png');
        background-repeat: no-repeat;
        background-position: center;
        background-size: 22px 13px;
        transition: transform .3s;
        transform: rotate(-90deg);

      }
      .arrow.active{
        transform: rotate(0deg);
      }
    }
    
    .textWrapper{
      box-sizing: border-box;
      font-size: 26px;
      color:#232323;
      padding: 35px 30px;
      background: #F4F5F7;
      line-height: 40px;
      .text {
        margin-bottom: 16px;
      }
      .text:last-child{
        margin-bottom: 0;
      }
    }
    .wrapper{
      transition: all .3s;
      overflow: hidden;
      height: 0;
    }
  }
  
}
</style>



